'use client';

import React, { useState } from 'react';
import Image from 'next/image';
import Link from 'next/link';
import Button from './Button';


export default function Header() {
    const [isMenuOpen, setIsMenuOpen] = useState(false);


    return (
        <header className="sticky top-0 z-50 bg-amber-50 backdrop-blur-md shadow-md p-4">
            <div className="flex justify-between mx-8">
                <Image src="/roofstack2.png" alt="RoofTrack Logo" width={360} height={240} className="w-40 md:w-60" />
                <div className="hidden md:block"> 
                    <Button variant="primary" className="ml-4 mt-10 self-center">Login</Button>
                </div>

                {/* Mobile Menu Button */}
                <button className="md:hidden flex flex-col gap-1"
                    onClick={() => setIsMenuOpen(!isMenuOpen)}
                    >
                    <span className="block w-6 h-0.5 bg-indigo-900"></span>
                    <span className="block w-6 h-0.5 bg-indigo-900"></span>
                    <span className="block w-6 h-0.5 bg-indigo-900"></span>
                </button>
            </div>
                <div className="hidden md:flex justify-between mx-8 mt-4">
                    <div>
                        <Button variant="secondary" className="ml-4">Product</Button>
                        <Button variant="secondary" className="ml-4">Features</Button>
                        <Button variant="secondary" className="ml-4">Resources</Button>
                    </div>
                    <div className="ml-64">
                        <Button variant="primary" className="ml-4">Request Demo</Button>
                        <Button variant="primary" className="ml-4">View Pricing</Button>
                    </div>
                </div>
            {/* Mobile Dropdown Menu */}
            {isMenuOpen && (
                <div className="md:hidden bg-amber-100 border-t border-amber-200">
                    <div className="flex flex-col p-4 space-y-3">
                        <Button variant="secondary" className="w-full" onClick={() => setIsMenuOpen(false)}>Product</Button>
                        <Button variant="secondary" className="w-full" onClick={() => setIsMenuOpen(false)}>Features</Button>
                        <Button variant="secondary" className="w-full" onClick={() => setIsMenuOpen(false)}>Resources</Button>
                        <Button variant="primary" className="w-full" onClick={() => setIsMenuOpen(false)}>Request Demo</Button>
                        <Button variant="primary" className="w-full" onClick={() => setIsMenuOpen(false)}>View Pricing</Button>
                        <Button variant="primary" className="w-full" onClick={() => setIsMenuOpen(false)}>Login</Button>
                    </div>
                </div>
            )}
        </header>
    );
}


